



























import AddTransducerModal from "@/components/transcuder-db-device/AddTransducerModal.vue";
import SelectTransducerModal from "@/components/transcuder-db-device/SelectTransducerModal.vue";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {TransducerDbSettingsSerializerWithPopulate} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {SelectTransducerModal, AddTransducerModal},
})
export default class DbSettingsInputView extends Vue {
  labId: string = "";
  chNum: string = "";
  data: TransducerDbSettingsSerializerWithPopulate[] = [];
  displaySelectModal: boolean = false;
  selectedTransducer: TransducerDbSettingsSerializerWithPopulate = null;
  selectedIndex: number = null;
  serial_num: string = "";
  beforeMount(): void {
    this.labId = String(this.$route.query.id);
    this.chNum = String(this.$route.query.ch);
    this.serial_num = String(this.$route.query.sn);
    this.getData();
  }

  getData(): void {
    this.api.getTDBSettingList({query: paramsToQuery({lab: this.labId})}).then((res) => {
      if (res.length === 0 && this.data.length === 0) {
        for (let i = 0; i < Number(this.chNum); i++) {
          this.data.push(null);
        }
      } else {
        this.data = res;
      }
    });
  }

  selectTransducer(transducer: TransducerDbSettingsSerializerWithPopulate, index: number): void {
    this.selectedTransducer = transducer;
    this.selectedIndex = index;
    this.displaySelectModal = true;
  }

  mockData = [
    {content: "Category : Loudspeaker / Name : Speaker.02 / Calibration Date : 03/28/2019, Select Detail Calibrate"},
    {
      content:
        "Category : Vibration Actuator / Name : Speaker.01 / Calibration Date : 03/27/2019, Select Detail Calibrate",
    },
    {
      content:
        "Category : Vibration Actuator / Name : Sensor01  / Calibration Date : 03/28/2019, Select Detail Calibrate",
    },
    {
      content:
        "Category : Electrical Output / Name : Sensor02  / Calibration Date : 04/01/2019, Select Detail Calibrate",
    },
  ];
}
